<template>
    <div class="coins-pairs-search">
        <div class="search">
         <!-- <p class="search-title">{{$localizeFilter('cryptocurrency_name') + ':'}}</p> -->
         <input class="search-field" v-model="searchString" v-maska="'AAAAA'" :placeholder="$localizeFilter('cryptocurrency_name')">
         <button class="basic-button search-button" @click="startSearch()">{{$localizeFilter('search_button')}}</button>
       </div>
       <div class="pairs"> 
            <div :class="`flex-row ${isSelected(pair) ? 'selected' : ''}`" v-for="(pair, idx) in visibleList" :key="idx" @click="selectPair(pair)">
                <div class="coin">
                    <img class="coin-image" :src="require(`@/assets/newImages/coins/icons/${$filters.lowercase(pair.firstCoin)}_icon.png`)">
                    <div class="coin-name">{{pair.firstCoin}}</div>
                </div>
                <div class="coin">
                    <img class="coin-image" :src="require(`@/assets/newImages/coins/icons/${$filters.lowercase(pair.lastCoin)}_icon.png`)">
                    <div class="coin-name">{{pair.lastCoin}}</div>
                </div>
            </div>
        </div>
        
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'

export default defineComponent({
    setup() {
        
    },
    data: ()=>({
      searchString: '',
      search: null,
    }),
    watch: {
        searchString(newVal, oldVal) {
            if (newVal === '') {
                this.$nextTick(()=>{
                    this.search = null
                })
            }
        }
    },
    computed: {
        coinsPairs() {
            return this.$store.getters.exchangeCoinsPairs
        },
        selectedCoinsPair() {
            return this.$store.getters.exchangeSelectedCoinsPair
        },
        visibleList() {
            const list = JSON.parse(JSON.stringify(this.coinsPairs))
            if (this.search == null) {
                return list
            } else {
                const result = []
                for (let i=0; i<list.length; i++) {
                    let pair = list[i]
                    // // console.log(pair.firstCoin, this.search, pair.firstCoin.startsWith(this.search))
                    if (pair.firstCoin.startsWith(this.search)) { result.push(pair) }
                }
                // // console.log(result)
                return result
            }
        },
    },
    methods: {
        startSearch() {
            this.search = this.searchString
            this.$forceUpdate()
        }, 
        async selectPair(pair) {
            await this.$store.dispatch('setSelectedCoinsPair', pair)
            this.$emit('loadData')
            setTimeout(()=>{
                this.$emit('closeModal')
                this.$router.push(`/cabinet/exchange?firstCoin=${this.selectedCoinsPair.firstCoin}&lastCoin=${this.selectedCoinsPair.lastCoin}`)
            }, 500)
        },
        isSelected(pair) {
            // console.log(pair)
            if (!this.selectedCoinsPair) { return false }
            return ((pair.firstCoin === this.selectedCoinsPair.firstCoin) && (pair.lastCoin === this.selectedCoinsPair.lastCoin))

        }
    }
})
</script>

